import type { SocialLoginProvider } from '@/lib/supabase/social/types';
import { getLastLoginInfo, setLastLoginInfo, clearLastLoginInfo, type LastLoginInfo } from './storage';

const LAST_LOGIN_PROVIDER_KEY = 'picnic_last_login_provider'; // Î†àÍ±∞Ïãú ÌÇ§ (ÎßàÏù¥Í∑∏Î†àÏù¥ÏÖòÏö©)

/**
 * ÏµúÍ∑º ÏÇ¨Ïö©Ìïú Î°úÍ∑∏Ïù∏ ÏàòÎã®ÏùÑ ÏÉàÎ°úÏö¥ storage ÏãúÏä§ÌÖúÏóê Ï†ÄÏû•Ìï©ÎãàÎã§.
 * @deprecated Ïù¥ Ìï®ÏàòÎäî Îçî Ïù¥ÏÉÅ ÏÇ¨Ïö©ÎêòÏßÄ ÏïäÏäµÎãàÎã§. Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ ÏãúÏóêÎßå ÏûêÎèôÏúºÎ°ú Ï≤òÎ¶¨Îê©ÎãàÎã§.
 */
export function saveLastLoginProvider(provider: SocialLoginProvider): void {
  console.warn('‚ö†Ô∏è [auth-helpers] saveLastLoginProviderÎäî Îçî Ïù¥ÏÉÅ ÏÇ¨Ïö©ÎêòÏßÄ ÏïäÏäµÎãàÎã§. Ìï®Ïàò Ìò∏Ï∂úÏù¥ Î¨¥ÏãúÎê©ÎãàÎã§.');
  // Ìï®Ïàò ÎÇ¥Ïö© ÏôÑÏ†ÑÌûà Ï†úÍ±∞ - Îçî Ïù¥ÏÉÅ Ï†ÄÏû• ÏûëÏóÖÏùÑ ÏàòÌñâÌïòÏßÄ ÏïäÏùå
  return;
}

/**
 * ÏµúÍ∑º ÏÇ¨Ïö©Ìïú Î°úÍ∑∏Ïù∏ ÏàòÎã®ÏùÑ ÏÉàÎ°úÏö¥ storage ÏãúÏä§ÌÖúÏóêÏÑú Í∞ÄÏ†∏ÏòµÎãàÎã§.
 */
export function getLastLoginProvider(): SocialLoginProvider | null {
  try {
    // ÏÉàÎ°úÏö¥ storage ÏãúÏä§ÌÖúÏóêÏÑú Î®ºÏ†Ä ÌôïÏù∏
    const lastLoginInfo = getLastLoginInfo();
    if (lastLoginInfo?.provider) {
      // Ïú†Ìö®Ìïú providerÏù∏ÏßÄ ÌôïÏù∏
      const validProviders: SocialLoginProvider[] = ['google', 'apple', 'kakao', 'wechat'];
      if (validProviders.includes(lastLoginInfo.provider as SocialLoginProvider)) {
        return lastLoginInfo.provider as SocialLoginProvider;
      }
    }

    // Î†àÍ±∞Ïãú ÏãúÏä§ÌÖúÏóêÏÑú ÌôïÏù∏ (ÎßàÏù¥Í∑∏Î†àÏù¥ÏÖòÏö© - ÏùΩÍ∏∞Îßå)
    if (typeof window !== 'undefined') {
      const lastProvider = localStorage.getItem(LAST_LOGIN_PROVIDER_KEY);
      if (lastProvider && ['google', 'apple', 'kakao', 'wechat'].includes(lastProvider)) {
        console.log('üîÑ [auth-helpers] Î†àÍ±∞Ïãú Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥ Î∞úÍ≤¨ - ÏùΩÍ∏∞Îßå ÏàòÌñâ');
        
        // Î†àÍ±∞Ïãú ÌÇ§ ÏÇ≠Ï†úÎßå ÏàòÌñâ (ÏÉàÎ°úÏö¥ ÏãúÏä§ÌÖúÏúºÎ°ú ÎßàÏù¥Í∑∏Î†àÏù¥ÏÖòÌïòÏßÄ ÏïäÏùå)
        localStorage.removeItem(LAST_LOGIN_PROVIDER_KEY);
        
        return lastProvider as SocialLoginProvider;
      }
    }
  } catch (error) {
    console.warn('ÏµúÍ∑º Î°úÍ∑∏Ïù∏ ÏàòÎã® Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:', error);
  }
  return null;
}

/**
 * ÏµúÍ∑º ÏÇ¨Ïö©Ìïú Î°úÍ∑∏Ïù∏ ÏàòÎã® Ï†ïÎ≥¥Î•º ÏÇ≠Ï†úÌï©ÎãàÎã§.
 */
export function clearLastLoginProvider(): void {
  try {
    // ÏÉàÎ°úÏö¥ storage ÏãúÏä§ÌÖúÏóêÏÑú ÏÇ≠Ï†ú
    clearLastLoginInfo();
    
    // Î†àÍ±∞Ïãú ÌÇ§ÎèÑ ÏÇ≠Ï†ú
    if (typeof window !== 'undefined') {
      localStorage.removeItem(LAST_LOGIN_PROVIDER_KEY);
    }
  } catch (error) {
    console.warn('ÏµúÍ∑º Î°úÍ∑∏Ïù∏ ÏàòÎã® ÏÇ≠Ï†ú Ïã§Ìå®:', error);
  }
}

/**
 * provider Î∞∞Ïó¥ÏùÑ ÏµúÍ∑º ÏÇ¨Ïö©Ìïú Í≤ÉÏùÑ Ïö∞ÏÑ†ÏàúÏúÑÎ°ú Ï†ïÎ†¨Ìï©ÎãàÎã§.
 */
export function sortProvidersByLastUsed(providers: SocialLoginProvider[]): SocialLoginProvider[] {
  const lastProvider = getLastLoginProvider();
  if (!lastProvider || !providers.includes(lastProvider)) {
    return providers;
  }
  
  // ÏµúÍ∑º ÏÇ¨Ïö©Ìïú providerÎ•º Îß® ÏïûÏúºÎ°ú Ïù¥Îèô
  return [lastProvider, ...providers.filter(p => p !== lastProvider)];
}

/**
 * ÏµúÍ∑º Î°úÍ∑∏Ïù∏ ÏàòÎã®Ïù¥ Ï†ÄÏû•ÎêòÏñ¥ ÏûàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§.
 */
export function hasLastLoginProvider(): boolean {
  return getLastLoginProvider() !== null;
}

/**
 * ÌäπÏ†ï providerÍ∞Ä ÏµúÍ∑º ÏÇ¨Ïö©Ìïú ÏàòÎã®Ïù∏ÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§.
 */
export function isLastUsedProvider(provider: SocialLoginProvider): boolean {
  return getLastLoginProvider() === provider;
}

/**
 * Provider Ïù¥Î¶ÑÏùÑ ÌïúÍµ≠Ïñ¥ ÌëúÏãúÎ™ÖÏúºÎ°ú Î≥ÄÌôòÌï©ÎãàÎã§.
 */
function getProviderDisplayName(provider: SocialLoginProvider): string {
  const displayNames: Record<SocialLoginProvider, string> = {
    google: 'Google',
    apple: 'Apple',
    kakao: 'Kakao',
    wechat: 'WeChat'
  };
  
  return displayNames[provider] || provider.charAt(0).toUpperCase() + provider.slice(1);
}

/**
 * Î°úÍ∑∏Ïù∏ ÏàòÎã® ÏÇ¨Ïö© ÌÜµÍ≥ÑÎ•º ÏúÑÌïú Í∞ÑÎã®Ìïú Ïπ¥Ïö¥ÌÑ∞ (ÏÑ†ÌÉùÏÇ¨Ìï≠)
 */
export function incrementProviderUsage(provider: SocialLoginProvider): void {
  try {
    if (typeof window !== 'undefined') {
      const key = `picnic_provider_usage_${provider}`;
      const currentCount = parseInt(localStorage.getItem(key) || '0', 10);
      localStorage.setItem(key, (currentCount + 1).toString());
    }
  } catch (error) {
    console.warn('Î°úÍ∑∏Ïù∏ ÏàòÎã® ÏÇ¨Ïö© ÌÜµÍ≥Ñ ÏóÖÎç∞Ïù¥Ìä∏ Ïã§Ìå®:', error);
  }
}

/**
 * Î™®Îì† Î°úÍ∑∏Ïù∏ Í¥ÄÎ†® ÌÜµÍ≥Ñ Îç∞Ïù¥ÌÑ∞Î•º ÏÇ≠Ï†úÌï©ÎãàÎã§.
 */
export function clearProviderUsageStats(): void {
  try {
    if (typeof window !== 'undefined') {
      const providers: SocialLoginProvider[] = ['google', 'apple', 'kakao', 'wechat'];
      providers.forEach(provider => {
        localStorage.removeItem(`picnic_provider_usage_${provider}`);
      });
    }
  } catch (error) {
    console.warn('Î°úÍ∑∏Ïù∏ ÏàòÎã® ÌÜµÍ≥Ñ ÏÇ≠Ï†ú Ïã§Ìå®:', error);
  }
} 